-727» компании American Airlines, летевшего из Чикаго в Вашингтон. Она могла бы уничтожить самолёт, но не взорвалась, хотя заполнила весь салон едким дымом, из-за чего экипажу пришлось срочно посадить судно. Только после этого ФБР занялось расследованием и обнаружило схожие черты в попытке подрыва самолёта и рассылке опасных коробок университетским сотрудникам. Дело назвали UNABOM (University and Airline Bomber — бомбист университетов и самолётов), а самого́ таинственного террориста в прессе стали называть Unabomber. Специалистам из Федерального бюро расследований долго не удавалось понять, кто совершает эти преступления. Пока они терялись в догадках, бомбы были доставлены ещё по 13 адресам, трое человек погибли, в двух случаях бомбу удалось обезвредить, в остальных не обошлось без травм: кому-то выбило глаз, кто-то потерял слух или пальцы на руках. Хотя некоторые бомбы были начинены щепками и гвоздями, они не были очень мощными и почему-то большинство их частей были деревянными. Днём 25 мая 1978 года на автостоянке Иллинойского университета в Чикаго появилась посылка без адресата, но с указанием отправителя: Бакли Крист, профессор материаловедения в Университете Норуэстерна в пригороде Чикаго. Поскольку доставлять её было некому, посылку вернули Кристу. Он, узнав об этом, заподозрил неладное, так как ничего не отправлял. Крист связался с отделением полиции при университетском кампусе. Как только офицер Терри Маркер открыл посылку, она взорвалась, повредив полицейскому левую руку. ")]),_c('p',[_vm._v(" Событие сочли случайной аномалией, отправителя коробки с бомбой не нашли, а через год, 9 мая 1979-го, случай повторился. На этот раз пострадал студент Университета Норуэстера Джон Харрис. Вскоре бомба похожей конструкции оказалась на борту «Боинга-727» компании American Airlines, летевшего из Чикаго в Вашингтон. Она могла бы уничтожить самолёт, но не взорвалась, хотя заполнила весь салон едким дымом, из-за чего экипажу пришлось срочно посадить судно. Только после этог